$ms: 375px;
$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

@media (max-width: $ms) {
  .hero-cta {
    .btn-sec {
      display: none;
    }
  }
  .menu-option {
    margin: 0 !important;
  }
}

@media (max-width: $sm) {
  .page-footer {
    text-align: center;
    .col-md-3 {
      margin-top: 1rem;
      .footer-nav {
        padding-bottom: 1rem;
        border-bottom: 1px solid #333;
        border-right: none !important;
        &:nth-child(2) {
          border-bottom: none !important;
        }
      }
    }
  }
  .blogs-container {
    flex-direction: column;
  }
}

@media (max-width: $md) {
  .hero-wrapper {
    background-color: $theme-color;
    .container {
      height: calc(100vh - 213px);
    }
    .hero-img {
      display: none;
    }
    .hero-cta {
      a:nth-of-type(1) {
        margin-right: 1rem !important;
      }
      .btn-main {
        background-color: $bg-primary;
        padding: 10px 2rem;
      }
      .btn-sec {
        border: 1px solid $bg-primary;
        padding: 10px 2rem;
      }
    }
  }
  .books-container {
    gap: 1rem !important;
  }
  .about-wrapper {
    .container {
      .about-desc {
        color: $text-primary;
        position: unset;
        width: 100%;
        padding: 0;
        transform: translate(0, 0);
        p {
          color: $text-primary;
        }
      }
      .about-img {
        display: none;
      }
    }
  }
  .collection-wrapper {
    .collection-img {
      display: none;
    }
  }
}

@media (max-width: $lg) {
  .faq-wrapper {
    .faq-container {
      flex-direction: column-reverse;
    }
  }
}

@media (max-width: $xl) {
}



